import React from "react";
import { saveAs } from "file-saver";

export function Generator() {
  const createAndDownloadFile = () => {
    const jsCode = `console.log('Hello, World!');`; // Replace with your JavaScript code
    const blob = new Blob([jsCode], { type: "application/javascript" });
    saveAs(blob, "myScript.js"); // Specify the file name
  };

  return (
    <div>
      <button onClick={createAndDownloadFile}>Create and Download File</button>
    </div>
  );
}
